
'use server';

import type { VtcStats, LeaderboardUser, Job, LiveDriver } from '@/lib/truckershub-types';

type TruckersHubResponse<T> = {
    status: boolean;
    response: T;
};

// This function should be executed on the server where process.env is available.
async function fetchFromTruckersHub<T>(endpoint: string, params?: URLSearchParams): Promise<T | null> {
    const apiKey = process.env.TRUCKERSHUB_API_KEY;
    if (!apiKey) {
        console.error("TRUCKERSHUB_API_KEY is not set.");
        return null;
    }

    try {
        const url = new URL(`${process.env.APP_URL}/api/truckershub`);
        url.searchParams.set('endpoint', endpoint);
        if (params) {
            params.forEach((value, key) => {
                url.searchParams.append(key, value);
            });
        }
        
        // Use a cache-sensitive fetch
        const res = await fetch(url.toString(), {
            next: { revalidate: 300 } // Revalidate every 5 minutes
        });


        if (!res.ok) {
            console.error(`Failed to fetch from TruckersHub (${endpoint}):`, res.status, await res.text());
            return null;
        }

        const data: TruckersHubResponse<T> = await res.json();
        
        if (data && data.status) {
            if (Array.isArray(data.response) && data.response.length === 0) return [] as T;
            if (typeof data.response === 'object' && data.response !== null && Object.keys(data.response).length === 0) return {} as T;
            return data.response;
        } else {
            // Avoid logging errors for empty but valid responses
            if (data.response && typeof data.response === 'object' && Object.keys(data.response).length > 0) {
                 console.error(`Invalid API response structure from TruckersHub (${endpoint}):`, data);
            }
            return null;
        }
    } catch (error) {
        console.error(`Error fetching from TruckersHub (${endpoint}):`, error);
        return null;
    }
}


export async function getDashboardData() {
    try {
        const [statsData, allTime, monthly, jobs, user, liveDrivers] = await Promise.all([
            fetchFromTruckersHub<{ vtc: VtcStats }>('vtc'),
            fetchFromTruckersHub<LeaderboardUser[]>('leaderboard/alltime'),
            fetchFromTruckersHub<LeaderboardUser[]>('leaderboard/monthly'),
            fetchFromTruckersHub<Job[]>('jobs/all'),
            fetchFromTruckersHub<{ username: string }>('user'),
            fetchFromTruckersHub<LiveDriver[]>('live/drivers'),
        ]);

        const stats = statsData ? statsData.vtc : null;
        
        return { stats, allTime, monthly, jobs, user, liveDrivers };
    } catch (error) {
        console.error("Failed to fetch dashboard data:", error);
        return { stats: null, allTime: null, monthly: null, jobs: null, user: null, liveDrivers: null };
    }
}

export async function getAllVtcJobs(month?: string, year?: string): Promise<Job[]> {
    const params = new URLSearchParams();
    if (month) params.append('month', month);
    if (year) params.append('year', year);
    return await fetchFromTruckersHub<Job[]>('jobs/all', params) || [];
}

export async function getMyJobs(): Promise<Job[]> {
    return await fetchFromTruckersHub<Job[]>('jobs') || [];
}
